import styles from "./Users.module.css";

import Pagination from "../common/Pagination/Pagination";
import Preloader from "../common/Preloader/Preloader";
import User from "./User";

import React, { useEffect } from "react";
import { usersType } from "../../types/types";
import { connect } from "react-redux";
import { globalStateType } from "../../redux/redux-store";
import {
  follow,
  unfollow,
  toggleIsFollowingProgress,
  getUsersThunkCreator,
  onPageChangedThunkCreator,
  followThunkCreator,
  unfollowThunkCreator,
} from "../../redux/usersReducer";
import {
  getCurrentPage,
  getFollowingInProgress,
  getIsFetching,
  getPageSize,
  getTotalUserCount,
  getUsers,
} from "../../redux/selectors/usersSelector";

type MapStateType = {
  users: Array<usersType>;
  pageSize: number;
  totalUserCount: number;
  currentPage: number;
  followingInProgress: Array<number>;
  isAuth: boolean;
  isFetching: boolean;
};
type MapDispatchType = {
  getUsersThunkCreator: (pageSize: number, currentPage: number) => void;
  onPageChangedThunkCreator: (pageSize: number, pageNumber: number) => void;
  followThunkCreator: (userId: number) => void;
  unfollowThunkCreator: (userId: number) => void;
};

type PropsType = MapStateType & MapDispatchType;

const Users: React.FC<PropsType> = ({
  users,
  pageSize,
  totalUserCount,
  currentPage,
  followingInProgress,
  isAuth,
  isFetching,
  getUsersThunkCreator,
  onPageChangedThunkCreator,
  followThunkCreator,
  unfollowThunkCreator,
}) => {
  useEffect(() => {
    getUsersThunkCreator(pageSize, currentPage);
  }, []);

  const onPageChanged = (pageNumber: number): void => {
    onPageChangedThunkCreator(pageSize, pageNumber);
  };

  return (
    <div className={styles.users}>
      {isFetching && <Preloader />}
      <Pagination
        onPageChanged={onPageChanged}
        totalItemsCount={totalUserCount}
        pageSize={pageSize}
        currentPage={currentPage}
        portionSize={10}
      />
      {users.map((u) => (
        <User
          user={u}
          key={u.id}
          isAuth={isAuth}
          followingInProgress={followingInProgress}
          followThunkCreator={followThunkCreator}
          unfollowThunkCreator={unfollowThunkCreator}
        />
      ))}
    </div>
  );
};

const mapStateToProps = (state: globalStateType) => {
  return {
    users: getUsers(state),
    pageSize: getPageSize(state),
    totalUserCount: getTotalUserCount(state),
    currentPage: getCurrentPage(state),
    isFetching: getIsFetching(state),
    followingInProgress: getFollowingInProgress(state),
    isAuth: state.auth.isAuth,
  };
};

export default connect(mapStateToProps, {
  follow,
  unfollow,
  followThunkCreator,
  toggleIsFollowingProgress,
  getUsersThunkCreator,
  onPageChangedThunkCreator,
  unfollowThunkCreator,
})(Users);
